.App{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(to right,#00aaff,#00ff6c);
  background-size: cover;
  background-position: center;
}
input{
  padding:15px;
  margin: 10px  0px;
  border-radius: 5px ;
  border: 1px solid grey;
}
form{
  background-color: antiquewhite;
  padding: 0px 60px;
  border-radius: 10px;
}
form>h1{
  color: rgb(73, 5, 73);
  text-align: center;
}

button{
  width:100%;
  height: 50px;
  padding: 10px;
  border: none;
  background-color: rebeccapurple;
  color:white;
  border-radius: 5px;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
  margin:15px 0px
}
.formInput{
  display: flex;
  flex-direction: column;
  width: 280px;
}
label{
  font-size:12px;
  color: gray;
}
span{
  font-size: 12px;
  padding: 3px;
  color: red;
  display:none;
}
input:invalid[focused="true"]~span{
  display:block;
  
}
input:invalid[focused="true"]{
  border: 1px solid red
}
input:invalid[focused=true]~span{
  display:block;
}
input:invalid[focused=true]{
  border:1px solid red
}

